<template>
    <div id="notice">
        <Card>
            <div class="news">
                <div class="title">
                    请输入新闻标题:
                    <Input v-model="newsInfo.title" size="large" placeholder="请输入标题" style="width: 300px"></Input>
                </div>     
                <mavon-editor style="z-index: 999;" :ishljs="true" ref=md @imgAdd="$imgAdd" v-model="newsInfo.body"></mavon-editor>         
                <div style="margin-top: 20px;text-align: center;">
                    <Button type="primary" @click="insertNews">{{modify? "修改": "发布"}}</Button>    
                </div>
            </div>           
            <div class="overNews" style="text-align:center;">
                <Table :columns="columns" :data="newsData" stripe></Table>   
                <Page v-if="pageLength[0]" :total="pageLength[0].COUNT" :page-size="5" class="page" @on-change="changePage" style="margin-top: 10px;"></Page> 
            </div>            
        </Card>
    </div>
</template>

<script>
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import filters from '@/filters/index'
export default {
    name: 'notice',
    data () {
        return {
            modify: false,
            pageLength: [],
            newsId: '',
            newsInfo: {
                body : '',
                title: ''
            },
            columns: [
                {
                    title: "编号",
                    key: 'id'
                },
                {
                    title: "标题",
                    key: 'title'
                },
                {
                    title: "发表时间",
                    key: 'post_time',
                    render: (h, params) => {                         
                        return h('div',
                            filters.timeFormat(params.row.post_time) 
                        )
                    } 
                },
                {
                    title: '删除',
                    key: 'action',
                    width: 150,
                    align: 'center',
                    render: (h, params) => {
                        return h('div', [     
                            h('Button', {
                                props: {
                                    type: 'success',
                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        this.modifyNews(params.row.id);
                                    }
                                }
                            }, '修改'),                           
                            h('Button', {
                                props: {
                                    type: 'error',
                                    size: 'small'
                                },
                                on: {
                                    click: () => {
                                        this.deleteNews(params.row.id);
                                    }
                                }
                            }, '删除')
                        ]);
                    }
                }
            ],
            newsData: []
        }
    },
    created () {
        this.getAllNews(1);
    },
    methods: {
        $imgAdd (pos, $file) {           
           var formdata = new FormData();
           formdata.append('image', $file);
           this.$http({
               url: '/file/dealPic',
               method: 'post',
               data: formdata,
               headers: { 'Content-Type': 'multipart/form-data' },
           }).then((res) => {                       
               this.$refs.md.$img2Url(pos, res.data.data);
           })
        },
        insertNews () {
            if(!this.newsInfo.title){
                this.$Message.error("标题不能为空！")
            }else if(!this.newsInfo.body){
                this.$Message.error("内容不能为空！")
            }else{
                if(this.modify){
                    this.$http.post('/news/updateNews',{
                        'newsId': this.newsId,
                        ...this.newsInfo
                    })
                    .then(res => {                        
                        this.$Message.success("修改成功!");
                        this.newsInfo.title = "";
                        this.newsInfo.body = "";
                        this.modify = false;
                        this.getAllNews(1);
                    }).catch(err => {
                        this.$Message.error("出错啦。。。");
                    })
                }else{
                    this.$http.post('/news/insertNews',this.newsInfo)
                    .then(res => {                        
                        this.$Message.success("发布成功！");
                        this.newsInfo.title = "";
                        this.newsInfo.body = "";
                        this.getAllNews();
                    }).catch(err => {
                        this.$Message.error("出错啦。。。");
                    })
                }
            }
        },
        getAllNews (page) {
            this.$http.get('/news/selectAllNews',{
                params: {
                    'page':page
                }
            }).then(res => {
                this.newsData = res.data.msg;
                this.pageLength = res.data.length;
            }).catch(err => {
                this.$Message.error("出错啦。。。");
            })
        },
        deleteNews (id) {
            this.$Modal.confirm({                
                content: '<p>确定删除吗</p>',
                onOk: () => {
                    this.$http.post('/news/deleteNews',{
                        'id': id                        
                    }).then(res => {                      
                        this.getAllNews();
                        this.$Message.success("删除成功！");
                    }).catch(err => {
                        this.$Message.error("出错啦。。。");
                    })
                },
                onCancel: () => {
                    this.$Message.info('Clicked cancel');
                }
            });
        },
        modifyNews (id) {            
            this.$Modal.confirm({                
                content: '<p>确定修改吗</p>',
                onOk: () => {
                    this.$http.get('/news/selectDetail',{
                        params: {
                            'newsId': id
                        }             
                    }).then(res => {     
                        let data = res.data.msg;                
                        this.newsInfo.title = data[0].title;
                        this.newsInfo.body = data[0].body;     
                        this.newsId = data[0].id; 
                        this.modify = true;   
                    }).catch(err => {
                        this.$Message.error("出错啦。。。");
                    })
                },
                onCancel: () => {
                    this.$Message.info('Clicked cancel');
                }
            });
        },
        changePage (page) {
            this.getAllNews(page);
        }
    },
    components: {
        mavonEditor
    }
}
</script>
<style lang="scss" scoped>
.news{    
    .title{
        margin: 16px 0 20px 0 ;
        
    }    
}
.overNews{
    margin-top: 30px;
}
</style>




